<template>
  <button
    class="btn btn-primary program-select-button"
    :class="buttonClass"
    @click="selectPlan"
  >
    <strong>{{ text }}</strong>
  </button>
</template>

<script>
export default {
  props: {
    plan: Object,
    text: String,
    buttonClass: { type: String, required: false }
  },
  methods: {
    selectPlan() {
      this.$store.dispatch("selectPlan", this.plan);
      this.$router.push("/enroll");
    }
  }
};
</script>

<style lang="scss">
@import "@/variables";

.program-select-button {
  .btn {
    background-color: $primary;
    border: none;
  }
  .btn:hover {
    background-color: $fucia;
  }
}
</style>
